.main {
    min-height: 100vh;
    padding: 2rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}

.item-card {
    min-height: 500px;
    width: 350px;
    margin: 12px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    box-shadow: 2px 2px 2px 2px  rgba(128, 128, 128, 0.146);
}

img {
    width: 100%;
    height: 330px;

}

.item-details {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin: 8px 0;
}

.item-details-address {
    font-size: 12px;
}

.card-price {
    font-size: 1.5rem;
    font-weight: 600;
    padding: 10px;
}

.card-description {
    padding: 10px;
    text-align: center;
    font-size: 20px;
}

.buy {
    background-color: green;
    padding: 3px 12px;
    margin: 10px;
    border-radius: 4px;
    color: white;
    cursor: pointer;
}

