@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Poppins&family=Ubuntu&display=swap);

.main {
    min-height: 100vh;
    padding: 2rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}

.item-card {
    min-height: 500px;
    width: 350px;
    margin: 12px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    box-shadow: 2px 2px 2px 2px  rgba(128, 128, 128, 0.146);
}

img {
    width: 100%;
    height: 330px;

}

.item-details {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin: 8px 0;
}

.item-details-address {
    font-size: 12px;
}

.card-price {
    font-size: 1.5rem;
    font-weight: 600;
    padding: 10px;
}

.card-description {
    padding: 10px;
    text-align: center;
    font-size: 20px;
}

.buy {
    background-color: green;
    padding: 3px 12px;
    margin: 10px;
    border-radius: 4px;
    color: white;
    cursor: pointer;
}


.trader {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    min-height: 100vh;
}

.trader-title {
    font-size: 40px;
    font-weight: 600;
}

.icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: green;
}

.trader-card {
    display: flex;
    justify-content: space-around;
    align-items: center;
    border: 1px solid gray;
    width: 500px;
    height: 80px;
    border-radius: 5px;
    margin: 8px;
}

.rate-button {
    background-color: green;
    padding: 5px 16px;
    color: white;
    margin-top: 5px;
    border-radius: 7px;
    cursor: pointer;
}
.form-new {
    display: flex;
    justify-content: center;
    align-items: center;
}

.form-form {
    border: 1px solid gray;
    width: 500px;
    padding: 25px;
    margin: 32px;
    border-radius: 10px;
}

* {
  font-family: 'Ubuntu', sans-serif;
}

